import QRCode from "qrcode";
// import { mapState } from "vuex";
import { BaseMixin } from "@/mixin/base.js";
import html2canvas from "html2canvas";
import BlindTimeText from "@/components/blindbox/blindTimeText.vue";
import { getImageInfo, Download, downloadFile, DPR } from "@/tools/common";
export default {
  name: "CrearePoster",
  mixins: [BaseMixin],
  components: { BlindTimeText },
  props: {
    locale:{
      default:"en"
    },
    zIndex: {
      default: 100
    },
    isBitKeep: {
      default: false
    },
    qrcodeText: {
      default: ""
    },
    info: {
      default: () => ({
        status: 0,
        is_owner: 0
      })
    },
    format: {
      default: () => "{dd}天{hh}小时{mm}分钟{ss}秒"
    },
    startTime: {
      default: () => 0
    },
    endTime: {
      default: () => 0
    }
  },
  computed: {
    proxy_img() {
      const sourceUrl = this.info.invite_image1 || "";
      return sourceUrl
        ? `/poster${sourceUrl
            .replace("http://cdn.bitkeep.vip", "")
            .replace("https://cdn.bitkeep.vip", "")}`
        : "";
    },
    codeText() {
      return this.qrcodeText || (process.client ? location.href : "");
    }
  },
  data() {
    return {
      showModal: false,
      qrcodeUrl: "",
      poster: {
        url: ""
      },
      pedding: false
    };
  },
  async mounted() {
    this.qrcodeUrl = await QRCode.toDataURL(this.codeText, {
      errorCorrectionLevel: "H",
      quality: 100,
      // margin: 5,
      color: {
        // // background: ;
        // dark:"#FFBF60FF",
        // light:"#ffffffb3"
      }
    }).catch(err => "");
  },
  methods: {
    init() {
      this.showModal = true;
      if (this.pedding) {
        this.showLoading(this.$t("ActivityBlindbox.toast.Generating"));
      }
    },
    async createPoster() {
      // if(!this.proxy_img && !this.info.invite_image1) return
      if (this.pedding)
        return this.showLoading(this.$t("ActivityBlindbox.toast.Generating"));

      this.showModal &&
        this.showLoading(this.$t("ActivityBlindbox.toast.Generating"));
      this.pedding = true;
      this.poster.url = "";
      const el = this.$refs.poster;

      if (!this.qrcodeUrl) {
        this.qrcodeUrl = await QRCode.toDataURL(this.codeText).catch(err => "");
        await this.$nextTick();
      }
      console.time("poster");
      console.log({ el: el.offsetWidth });
      html2canvas(el, {
        dpi: DPR(),
        // backgroundColor: "#fff",
        // useCORS: true,
        // windowWidth: document.body.scrollWidth,
        // windowHeight: document.body.scrollHeight,
        width: el.offsetWidth - 1,
        height: el.offsetHeight - 1,
        scale: 3,
        async: true,
        // width: 375,
        // height: 812,
        scrollY: 0,
        scrollX: 0,
        allowTaint: false
      })
        .then(async canvas => {
          // canvas.toBlob((blob) => {
          //   console.log({blob})
          // })
          // canvas.toDataURL("image/png");
          // const wid = canvas.width;
          // const hei = canvas.height;
          // const img = html2canvas.convertToJPEG(canvas, wid, hei);
          // const dataURL = img.getAttribute("src");
          this.poster.url = canvas.toDataURL("image/jpeg");
          await this.$nextTick();
          this.pedding = false;
          console.timeEnd("poster");
          console.time("upload");
          this.hideLoading();
        })
        .catch(err => {
          this.pedding = false;
          this.$toast.fail(JSON.stringify(err));
        });
    },
    shareImage() {
      BitKeepInvoke.shareUrl(
        '',
        this.$t("ActivityBlindbox.shared.content"),
        location.href,
        this.info.cover_image,
        console.log
      );
    },
    saveImage() {
      if (this.poster.url) {
        this.showLoading(this.$t("ActivityBlindbox.toast.Saving"));
        BitKeepInvoke.saveImageFromBase64(this.poster.url, e => {
          this.hideLoading();
        });
        return;
      } else {
        this.createPoster();
      }
      // this.createPoster();
    }
  }
};
