import Countdown from "@/components/common/c-vue-countdown";
export default {
  name: "blindTimeText",
  components: { Countdown },
  props: {
    locale:{
      type: String,
      default: () => "en"
    },
    info: {
      type: Object,
      default: () => ({
        status: 0,
        is_owner:0
      })
    },
    startTime: {
      type: Number || String,
      default: 0
    },
    endTime: {
      type: Number || String,
      default: 0
    },
    format: {
      type: String,
      default: "{dd}天{hh}小时{mm}分钟{ss}秒"
    }
  },
  methods: {
    chountChange(...arg) {
      this.$emit("change", ...arg);
    },
    end(...arg) {
      this.$emit("end", ...arg);
    }
  }
};
